//function which removes all text after " "
export function removeAfterSpace(text) {
    return text.substring(0, text.indexOf(" "));
    }



export function showLanguageWithEmoji(language) {

    switch (language) {
        case "en":
            return "English ";
        case "fr":
            return "Franais ";
        case "de":
            return "Deutsch ";
        case "it":
            return "Italiani ";
        case "es":
            return "Espaol ";
        case "ja-Hrkt":
            return " ";
        case "ko":
            return " ";
        default:
            return "-";
    }
}